.Home .App-intro {
  font-size: large; }

.Home main {
  display: flex;
  margin-top: 0.5in;
  margin-bottom: 0.5in;
  flex-direction: column;
  justify-content: center; }

.Home aside {
  margin: 0 0.5in calc(0.5in - 0.5rem); }

@media (min-width: 768px) {
  .Home main {
    margin-top: 1in;
    margin-bottom: 1in; }
  .Home aside {
    margin: 0 1in; } }

.Home .logos {
  align-items: center;
  display: flex;
  margin: 0 -0.5in;
  padding: 0 calc(0.5in - 15px);
  flex-wrap: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch; }

@media (min-width: 768px) {
  .Home .logos {
    margin: 0 -1in;
    padding: 0 calc(1in - 15px); } }

.Home .logos .logo {
  margin-bottom: 0.5rem; }

.Home .logos .logo {
  margin-left: 15px;
  margin-right: 15px; }

.Home .logos div:last-child {
  padding-right: 0.5in; }

@media (min-width: 992px) {
  .Home .logos div:last-child {
    padding-right: 0; } }

.Home .bootstrap-expo {
  color: #563d7c;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.5em;
  font-weight: 300;
  white-space: nowrap; }
